/**
 * Place any jQuery/helper plugins in here.
 */
(function ($) {
    /**
     * Checkbox tree for permission selecting
     */
    let permissionTree = $(".permission-tree :checkbox");

    permissionTree.on("click change", function () {
        if ($(this).is(":checked")) {
            $(this)
                .siblings("ul")
                .find('input[type="checkbox"]')
                .attr("checked", true)
                .attr("disabled", true);
        } else {
            $(this)
                .siblings("ul")
                .find('input[type="checkbox"]')
                .removeAttr("checked")
                .removeAttr("disabled");
        }
    });

    permissionTree.each(function () {
        if ($(this).is(":checked")) {
            $(this)
                .siblings("ul")
                .find('input[type="checkbox"]')
                .attr("checked", true)
                .attr("disabled", true);
        }
    });

    /**
     * Disable submit inputs in the given form
     *
     * @param form
     */
    function disableSubmitButtons(form) {
        form.find('input[type="submit"]').attr("disabled", true);
        form.find('button[type="submit"]').attr("disabled", true);
    }

    /**
     * Enable the submit inputs in a given form
     *
     * @param form
     */
    function enableSubmitButtons(form) {
        form.find('input[type="submit"]').removeAttr("disabled");
        form.find('button[type="submit"]').removeAttr("disabled");
    }

    /**
     * Disable all submit buttons once clicked
     */
    $("form").submit(function () {
        disableSubmitButtons($(this));
        return true;
    });

    /**
     * Add a confirmation to a delete button/form
     */
    $("body")
        .on("click", "a[data-method=delete]", function (e) {
            e.preventDefault();
            const button = $(this);
            const href = button.attr("href");
            Swal.fire({
                text: "Are you sure you want to delete this item?",
                showCancelButton: true,
                confirmButtonText: "Confirm Delete",
                cancelButtonText: "Cancel",
                icon: "warning",
            }).then((result) => {
                if (result.isConfirmed == true) {
                    if (result.value) {
                        axios.delete(href).then((response) => {
                            let res = response.data;
                            if (res.status) {
                                Swal.fire({
                                    icon: res.icon,
                                    text: res.msg,
                                });
                                setTimeout(function () {
                                    window.location.reload();
                                }, 1000);
                                button.closest("tr").remove();
                            } else {
                                Swal.fire({
                                    icon: res.icon,
                                    text: res.msg,
                                });
                                setTimeout(function () {
                                    window.location.reload();
                                }, 1000);
                            }
                        });
                    }
                }

            });
        })

        .on("click", "a[data-method=delete-post]", function (e) {
            e.preventDefault();
            const button = $(this);
            const href = button.attr("href");
            Swal.fire({
                text: "Are you sure you want to delete this item?",
                showCancelButton: true,
                confirmButtonText: "Confirm Delete",
                cancelButtonText: "Cancel",
                icon: "warning",
            }).then((result) => {
                if (result.isConfirmed == true) {
                    if (result.value) {
                        axios.post(href).then((response) => {
                            let res = response.data;
                            if (res.status) {
                                Swal.fire({
                                    icon: res.icon,
                                    text: res.msg,
                                });
                                setTimeout(function () {
                                    window.location.reload();
                                }, 1000);
                                button.closest("tr").remove();
                            } else {
                                Swal.fire({
                                    icon: res.icon,
                                    text: res.msg,
                                });
                                setTimeout(function () {
                                    window.location.reload();
                                }, 1000);
                            }
                        });
                    }
                }

            });
        })
        .on("submit", "form[name=confirm-item]", function (e) {
            e.preventDefault();
            Swal.fire({
                title: "Are you sure you want to do this?",
                showCancelButton: true,
                confirmButtonText: "Continue",
                cancelButtonText: "Cancel",
                icon: "warning",
            }).then((result) => {
                if (result.value) {
                    this.submit();
                } else {
                    enableSubmitButtons($(this));
                }
            });
        })
        .on("click", "a[name=confirm-item]", function (e) {
            /**
             * Add an 'are you sure' pop-up to any button/link
             */
            e.preventDefault();

            Swal.fire({
                title: "Are you sure you want to do this?",
                showCancelButton: true,
                confirmButtonText: "Continue",
                cancelButtonText: "Cancel",
                icon: "info",
            }).then((result) => {
                result.value && window.location.assign($(this).attr("href"));
            });
        });

    // Remember tab on page load
    $('a[data-toggle="tab"], a[data-toggle="pill"]').on(
        "shown.bs.tab",
        function (e) {
            let hash = $(e.target).attr("href");
            history.pushState ?
                history.pushState(null, null, hash) :
                (location.hash = hash);
        }
    );

    let hash = window.location.hash;
    if (hash) {
        $('.nav-link[href="' + hash + '"]').tab("show");
    }

    // Enable tooltips everywhere
    $('[data-toggle="tooltip"]').tooltip();
})(jQuery);
