const { default: axios } = require("axios");
// const numWords = require('num-words');
const { toBengaliNumber, toBengaliWord} = require('bengali-number');
const { default: Swal } = require("sweetalert2");


(function ($) {
   
    $("body")
        .on('change', '#plot_length_width', function () {
            const route = $('#plot-length-width').val();
            const plotId = $(this).val();
            axios.post(route, { 'id': plotId }).then((response) => {
                let res = response.data;
                if (res) {
                    $('#plot_width').val(toBengaliNumber(res.plot_width));
                    $('#plot_length').val(toBengaliNumber(res.plot_length));
                    $('#total_sft').val(toBengaliNumber(res.total_sft));
                    $('#total_sft_numeric').val(res.total_sft);
                } else {
                    var error = res.errors;
                    console.log(error);
                }
            });
        })

        .on('keyup', '#per_acore_amount', function () {
            const amount = $(this).val();
            const station_tender_rate = $('#station_tender_rate').val();
            const total_sft_numeric = $('#total_sft_numeric').val();
            const total_amount = parseInt(amount) * parseInt(total_sft_numeric);
            const acr_percentage = (total_amount * 25) / 100;

            if (isNaN(amount)) {
                Swal.fire({
                    icon: "warning",
                    text: "নম্বরটি সঠিক নয়",
                    showCancelButton: false,
                });
            } else {
                if (parseInt(amount) < parseInt(station_tender_rate)) {
                    $("#per_acore_amount").addClass("is-invalid");
                } else {
                    $("#per_acore_amount").removeClass("is-invalid");
                    $("#plot_total_onetimeamout").val(toBengaliNumber(total_amount));
                    $("#plot_total_onetimeInword").val(toBengaliWord(total_amount));
                    $("#per_acore_percentage").val(toBengaliNumber(acr_percentage));
                    $("#per_acore_percentage_inword").val(toBengaliWord(acr_percentage));
                }
            }
        })

        .on('keyup', '.transform_number_one', function () {
            const value = $(this).val();
            const bengaliNumberRepresentation = toBengaliNumber(value);
            const bengaliNumberToWord = toBengaliWord(value);
            // $('.transform_number_one').val(bengaliNumberRepresentation);
            $('.transform_number_value_one').val(bengaliNumberToWord);
        })
        .on('keyup', '.transform_number_two', function () {
            const bengaliNumberRepresentation = toBengaliNumber($(this).val());
            const bengaliNumberToWord = toBengaliWord($(this).val());
            // $('.transform_number').val(bengaliNumberRepresentation);
            $('.transform_number_value_two').val(bengaliNumberToWord);
        })
        .on('keyup', '.transform_number_three', function () {
            const bengaliNumberRepresentation = toBengaliNumber($(this).val());
            const bengaliNumberToWord = toBengaliWord($(this).val());
            // $('.transform_number').val(bengaliNumberRepresentation);
            $('.transform_number_value_three').val(bengaliNumberToWord);
        })
        .on('keyup', '.transform_number_four', function () {
            const bengaliNumberRepresentation = toBengaliNumber($(this).val());
            const bengaliNumberToWord = toBengaliWord($(this).val());
            // $('.transform_number').val(bengaliNumberRepresentation);
            $('.transform_number_value_four').val(bengaliNumberToWord);
        });
        
})(jQuery);
